document.addEventListener('DOMContentLoaded', () => {
    iniciarApp();
})

function iniciarApp(){
    buscarPorFecha();
}

function buscarPorFecha() {
    /**Seleccionar el input date de la vista /admin/index */
    const inputFecha = document.querySelector('#fecha');

    inputFecha.addEventListener('input', e => {
        fecha = e.target.value; //Obtener el valor seleccionado
        window.location = `?fecha=${fecha}`; //Enviar la fecha por la url
    })
}